/* Header section */
.brand{
    font-size: 1rem;
    color: #ffff;
    text-decoration: none;
    font-weight: 700;
}
#header{
    position: -webkit-sticky;
    position: sticky;
    z-index: 1000;
    left: 0;
    top: -1px;
    width: 100%;
}
#header .header{
    background-color:rgba(145, 9, 30,.4);
    transition: .4s ease background-color;
}
#header .header.colorChange{
    background-color:#212529;
}
#header .nav-bar{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    height: 100%;
    padding: 0 10px;
}
#header .nav-list ul{
    list-style: none;
    position: absolute;
    background: url(/newforyou/static/media/navimg.d4aaed21.webp);
    background-size:  cover;
    background-repeat: no-repeat;
    width: 80%;
    height: 80vh;
    left: -100%;
    top: -1000%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 0;
    z-index: 1;
    overflow-x: hidden;
    transition: left .5s ease,top .5s ease ;
    border-bottom-right-radius: 50%;
    /* transition: .5s ease top; */
}
#header .nav-list ul::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 80vh;
    background-color: var(--primary-color);
    opacity: .7;
    z-index: -1;
}
#header .nav-list ul.active{
    left: 0;
    top:0
}
#header .nav-list ul .mynav-links{
    font-size: 1.1rem;
    font-weight: 500px;
    letter-spacing: .1rem;
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    padding: 10px;
    display: block;
    margin: 0 5px;
}
/* search bar css */
#header .nav-list ul .news-serch-form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
#header .nav-list ul .news-serch-form .news-search-bar{
    outline: none;
    width: 250px;
    margin: 5px 0px;
    border: 2px solid #ffff;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    padding: 8px 10px;
    background-color: transparent;
    color: #ffff;
}
#header .nav-list ul .news-serch-form .news-search-bar::-webkit-input-placeholder{
    font-size: .9rem;
    color: #ffff;
}
#header .nav-list ul .news-serch-form .news-search-bar:-ms-input-placeholder{
    font-size: .9rem;
    color: #ffff;
}
