    font-size: .9rem;
    color: #ffff;
}
#header .nav-list ul .news-serch-form .search-btn{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 8px 10px;
    background-color: transparent;
    border: 2px solid #fff;
    color: #fff;
    transition: all .4s;
