}

/* end search bar css */
.mynav-links:hover {
  color: var(--primary-color);
  transition: all 0.3s ease-out;
}
#header .nav-list .hamburger{
    height: 60px;
    width: 60px;
    border: 3px solid #ffff;
    border-radius: 20%;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    z-index: 100;
    cursor: pointer;
    -webkit-transform: scale(.8);
            transform: scale(.8);
    margin-right: 5px;
}
#header .nav-list .hamburger:after{
    position: absolute;
    height: 100%;
    width: 100%;
    content: '';
    border-radius: 50%;
    border: 3px solid #fff;
    -webkit-animation: hamburger-pulse 1s ease infinite;
            animation: hamburger-pulse 1s ease infinite;
}
#header .nav-list .hamburger .fa-times{
  color: #ffff;
  font-size: 30px;
}
#header .nav-list .hamburger .fa-bars{
  color: #ffff;
  font-size: 30px;
}
